<template>
  <div id="app">
    
    <div class="w-full flex justify-center">
      <div class="w-full flex flex-wrap max-w-5xl">
        <div class="w-full font-bold text-right text-xl mr-10">Time Left : 20 s</div>
      </div>
    </div>
    <div class="w-full flex justify-center">
      <div class="w-full flex flex-wrap max-w-5xl">
        <div class="p-2" v-for="(item, index) in photos" :key="index">

          <div class="flip-box rounded-lg overflow-hidden" @click="addfilp(item)">
            <div :class="item.isFilp ? 'flip-now':''" class="flip-box-inner">
              <div class="flip-box-front">
                <img class="" src="./assets/img/logo.png" alt="musab">
              </div>
              <div class="flip-box-back">
                <img class="w-full h-full object-center" :src="item.imgPath" alt="musab">
              </div>
            </div>
          </div>

        </div>
      </div>
    </div>
  
  </div>
</template>

<script>

export default {
  name: 'App',
  components: {
  },
  data() {
    return {
      hasfilp:false,
      first:null,
      scound:null,
      photos:[]
    }
  },
  created() {
    this.addImg();
  },
  methods: {
    addfilp(obj){
      if(this.first != null && this.scound != null)return;
      if(obj.isFilp == true)return;
      obj.isFilp = true
      if (!this.hasfilp) {
        this.first = obj
        this.hasfilp = true;
      }else{
        this.scound = obj
        this.hasfilp = false;
        this.jeck();
      }
    },
    jeck(){
      var vm = this;
      if (vm.first.id == vm.scound.id) {
        vm.first = null;
        vm.scound = null;
        console.log(vm.first.id +":"+ vm.scound.id)
        this.ifAllDone();
      }else{
        setTimeout(function() {
          vm.first.isFilp = false;
          vm.scound.isFilp = false;
          vm.first = null;
          vm.scound = null;
        }, 2000);
      }
    },
    ifAllDone(){
      var done = true;
      this.photos.forEach(item => {
        if(item.isFilp != true){
          done = false;
          return false;
        }
      });
      if(done) console.log("allFilpDone");
    },
    addImg(){
      for (let n = 0; n < 18; n++) {
        this.photos.push({
          id: Math.floor(n/2),
          imgPath:require("./assets/img/"+Math.floor(n/2)+".jpg"),
          isFilp:false
        });
      }
    }
  },
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}


.flip-box{
  background-color: transparent;
  width: 150px;
  height: 150px;
  border: 1px solid #f1f1f1;
  perspective: 1000px;
}

.flip-box-inner{
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 2.0s;
  transform-style: preserve-3d;
}

.flip-now{
  transform: rotateY(180deg);
}

.flip-box-front, .flip-box-back{
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.flip-box-front{
  background-color: #bbb;
  color: black;
}

.flip-box-back{
  background-color: #555;
  color: white;
  transform: rotateY(180deg);
}
</style>
